/* Components */
/**
 * contact
 */
.contact__subheader {
  font-size: 1.3125rem;
  font-weight: 500;
  margin-bottom: 2.5rem;
}

.contact__email-title {
  font-size: 1rem;
  font-weight: 500;
  margin: 1.4375rem 0 0.1875rem 0;
}

.contact__icon-3-stars {
  font-size: 4px;
  position: absolute;
  right: -3.375rem;
  top: 11.125rem;
}

@media only screen and (max-width: 1024px) {
  .contact__icon-3-stars {
    right: calc(50% - 165px);
    top: 6.0625rem;
  }
}

@media only screen and (max-width: 374px) {
  .contact__icon-3-stars {
    right: calc(50% - 145px);
  }
}

.contact__icon-ok-hand {
  font-size: 4px;
  left: -2.8125rem;
  position: absolute;
  top: -3.375rem;
  z-index: 1;
}

.contact__email-link {
  color: #ed3fa8;
  font-size: 0.875rem;
  font-weight: 500;
  margin: 0;
  text-decoration: underline;
}

.contact__content {
  color: #8b8b8b;
  font-size: 0.875rem;
  font-weight: 500;
  line-height: 1.4375rem;
  margin: 0 auto 3.75rem;
  width: 35.625rem;
}

.contact__content a {
  color: #ed3fa8;
  font-weight: 500;
  text-decoration: underline;
}

@media only screen and (max-width: 1024px) {
  .contact__content {
    width: 100%;
  }
}

.contact__form-container {
  margin: 0 auto;
  position: relative;
  width: 60%;
}

@media only screen and (max-width: 1024px) {
  .contact__form-container {
    width: 80%;
  }
}

.contact__form .form-item {
  position: relative;
}

.contact__submit-container {
  bottom: -0.25rem;
  position: absolute;
  right: -0.5625rem;
}

.contact__form-email-container {
  display: flex;
  margin: 0 auto 3.125rem;
  max-width: 60.125rem;
}

@media only screen and (max-width: 1024px) {
  .contact__form-email-container {
    align-items: center;
    flex-direction: column;
    margin-bottom: 0;
  }
}

.contact__email-container {
  display: flex;
  justify-content: flex-end;
  margin: 2.6875rem 3.5625rem 3.4375rem 0;
  position: relative;
  width: 40%;
}

@media only screen and (max-width: 1024px) {
  .contact__email-container {
    align-items: center;
    justify-content: center;
    margin: 2.6875rem auto 3.4375rem;
    width: 100%;
  }
}

.contact__response-container {
  color: #2e8b57;
  height: 100px;
}

.contact__response-text {
  background-color: #e2ffef;
  display: none;
  margin: 0;
  padding: 2.5rem 0;
  text-align: center;
}

.error-message {
  color: #e20000;
  font-size: 0.8125rem;
  padding-top: 0.3125rem;
}
